<template>
  <div class="index__videoPage__src-videoPage-">
    <head-bar></head-bar>
    <video-pro :aid="aid" :pic="pic"></video-pro>
    <video-area></video-area>
    <video-info :info="info"></video-info>


  </div>
</template>

<script>
  import axios from '../utils/v_axios';
  import headBar from '../components/main/header.vue';
  import videoPro from '../components/video/video.vue';
  import videoArea from '../components/video/videoArea.vue';
  import videoInfo from '../components/video/videoInfo.vue';

  export default {
    data () {
      return {
        videoData: [],
        aid: '',
        info:'',
        pic:''
      }
    },
    created(){
      var that = this;
      that.axios = axios;
      that.load();
    },
    mounted () {

    },
    methods: {
      load(){
        var that = this;
        that.aid = that.$route.params.aid;
        that.axios.get(`https://api.imjad.cn/bilibili/v2/?aid=${that.aid}`).then((res) => {
          window.document.title = res.data.data.title;
          that.videoData = res.data.data;
          that.pic = res.data.data.pic;
          that.info = {
              title:that.videoData.title,
              tname:that.videoData.tname,
              view:that.videoData.stat.view,
              danmaku:that.videoData.stat.danmaku,
              aid:that.videoData.aid,
              desc:that.videoData.desc
          }
        })
      }
    },
    components: {headBar, videoPro ,videoArea ,videoInfo},
  }
</script>

<style scoped>




</style>



