              class="player-icon icon-widescreen"></i></span><span class="control-btn btn-fullscreen active hide"><i
              class="player-icon icon-fullscreen"></i></span></div>
            <div class="control-slider">
              <div class="ui-slider-container">
                <div class="ui-slider-tracker"></div>
                <div class="ui-slider-label">: 30.518828</div>
                <div class="ui-slider-thumb" style="width: 89.2285%;"></div>
                <div class="ui-slider-event-layer"></div>
              </div>
