<script>
  import flv from 'flv.js';
  import axios from '../../utils/v_axios';
  export default {
    data () {
      return {
        video: '',
        videoState: true,
        loading: false,
        loadLayer: false,
        ended:true,
        flvPlayer:'',
        down:true,
        iconState:false,
        timer:null,
        url:''
      }
    },
    created(){
      var that = this;
      that.flv = flv;
      that.axios = axios;
    },
    mounted () {
      this.getUrl();
      this.canplay();
    },
    props:{
       aid:{
           default:''
       },
       pic:{
           default:''
       }
    },
    methods: {
      getUrl(){
          var that = this;

      },
      canplay(){
        var that = this;
        that.axios.get(`https://api.imjad.cn/bilibili/v2/?aid=${that.aid}&page=1&quality=2`).then((res) => {
          that.url = res.data.durl[0].url;
          that.video = that.$refs.myvideo;
          if (that.flv.isSupported()) {
            that.flvPlayer = that.flv.createPlayer({
              type: 'mp4',
              url: that.url
            });
            that.flvPlayer.attachMediaElement(that.video);
            that.flvPlayer.load();
          }
          that.video.addEventListener('canplay', () => {   //
            that.loading = true;

          });
          that.video.addEventListener('ended', () => {   //
            that.ended = false;
            that.videoState = true;
          });
          that.video.addEventListener('timeupdate',() => {  //

          });
          that.video.addEventListener('pause',() => {

            that.iconState = false;
            that.timer = null;
          })
        })
      },
      play(type){
        var that = this;
        switch (type){
          case 'start' :
            that.iconState = true;
            break;
        }

        that.flvPlayer.play();
        that.videoState = false;
        that.loadLayer = true;
        that.down = false;
        if(!that.ended){
          that.ended = true;
        }
      },
      pause(){
        var that = this;
        that.flvPlayer.pause();
        that.videoState = true;
        that.timer = null;
        that.iconState = true;
      },
      controllerState(){
        var that = this;
        that.iconState = !that.iconState;
        that.timer = null;
        if(!that.iconState){
          that.timer = setTimeout(() => {
            that.iconState = true;
            that.timer = null;
          },3000);
        }
      }
    },
  }

</script>

